@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
  text-align: justify;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
  padding: 0;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1,
h2,
h3,
h4,
a {
  font-family: "Lato", sans-serif;
  margin: 1.5rem 0;
  font-weight: bold;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 1.5rem;
  margin-bottom: 1.5rem;
}

.contenido-centrado {
  max-width: 100rem;
}

.alinear-derecha {
  display: flex;
  justify-content: flex-end;
}

/* Document
 * ========================================================================== */
/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */
html {
  line-height: 1.15;
  /* 2 */
}

/* Sections
 * ========================================================================== */
/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */
h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */
/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */
dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */
ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Correct the inheritance of border color in Firefox.
 * 3. Show the overflow in Edge 18- and IE.
 */
hr {
  box-sizing: content-box;
  /* 1 */
  color: inherit;
  /* 2 */
  height: 0;
  /* 1 */
  overflow: visible;
  /* 3 */
}

/**
 * Add the correct display in IE.
 */
main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
pre {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

/* Text-level semantics
 * ========================================================================== */
/**
 * Remove the gray background on active links in IE 10.
 */
a {
  background-color: transparent;
}

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */
abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */
b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */
code,
kbd,
samp {
  font-family: monospace, monospace;
  /* 1 */
  font-size: 1em;
  /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */
small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */
/**
 * Add the correct display in IE 9-.
 */
audio,
video {
  display: inline-block;
}

/**
 * Add the correct display in iOS 4-7.
 */
audio:not([controls]) {
  display: none;
  height: 0;
}

/**
 * Remove the border on images within links in IE 10-.
 */
img {
  border-style: none;
}

/**
 * Hide the overflow in IE.
 */
svg:not(:root) {
  overflow: hidden;
}

/* Tabular data
 * ========================================================================== */
/**
 * 1. Correct table border color inheritance in all Chrome, Edge, and Safari.
 * 2. Remove text indentation from table contents in Chrome, Edge, and Safari.
 */
table {
  border-color: inherit;
  /* 1 */
  text-indent: 0;
  /* 2 */
}

/* Forms
 * ========================================================================== */
/**
 * Remove the margin on controls in Safari.
 */
button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */
button {
  overflow: visible;
  /* 1 */
  text-transform: none;
  /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */
button,
[type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */
fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */
input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */
legend {
  box-sizing: border-box;
  /* 1 */
  color: inherit;
  /* 2 */
  display: table;
  /* 1 */
  max-width: 100%;
  /* 1 */
  white-space: normal;
  /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */
progress {
  display: inline-block;
  /* 1 */
  vertical-align: baseline;
  /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */
select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */
textarea {
  margin: 0;
  /* 1 */
  overflow: auto;
  /* 2 */
}

/**
 * 1. Add the correct box sizing in IE 10-.
 * 2. Remove the padding in IE 10-.
 */
[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  /* 1 */
  padding: 0;
  /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */
[type=search] {
  -webkit-appearance: textfield;
  /* 1 */
  outline-offset: -2px;
  /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */
::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */
::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */
::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */
::-webkit-file-upload-button {
  -webkit-appearance: button;
  /* 1 */
  font: inherit;
  /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */
::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */
:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */
:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */
/*
 * Add the correct display in Edge 18- and IE.
 */
details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */
dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */
summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */
/**
 * Add the correct display in IE 9-.
 */
canvas {
  display: inline-block;
}

/**
 * Add the correct display in IE.
 */
template {
  display: none;
}

/* User interaction
 * ========================================================================== */
/**
 * Add the correct display in IE 10-.
 */
[hidden] {
  display: none;
}

.darkMode {
  background-color: #1a1a1a;
}
.darkMode p {
  color: #FFFFFF;
}
.darkMode a, .darkMode h1, .darkMode h2, .darkMode h3, .darkMode h4 {
  color: #c0c0c0;
}
.darkMode .anuncio {
  background-color: #333333;
}
.darkMode .anuncio h4, .darkMode .anuncio p {
  color: #FFFFFF;
}

.no-webp .inicio {
  background-image: url(../img/Lubricantes/aceiteTransparente.png);
}

.webp .inicio {
  background-image: url(../img/Lubricantes/aceiteTransparente.webp);
}

.header {
  background-color: rgb(30, 255, 0);
  padding: 1rem 0 2rem 0;
}
.header.inicio {
  background-position: center center;
  background-size: cover;
  background-size: 80%;
  background-repeat: no-repeat;
}
@media (min-width: 768px) {
  .header.inicio {
    min-height: 60rem;
  }
}
@media (min-width: 768px) {
  .header.inicio .contenedor-header {
    min-height: 70rem;
  }
}

.contenedor-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.contenedor-header h3 {
  text-align: left;
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  font-size: 2rem;
  margin-left: 2rem;
  max-width: 40rem;
}

.barra {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin: 0 auto;
}
.barra img {
  max-width: 20rem;
  margin: auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 15px;
  animation: mymove 5s infinite;
}
@keyframes mymove {
  50% {
    background-color: rgba(247, 235, 15, 0.2);
  }
}
.barra .titulo-principal {
  color: rgb(92, 92, 45);
  letter-spacing: 0.3rem;
}
.barra .titulo-principal h2 {
  font-size: 4rem;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-end;
  }
  .barra .titulo-principal {
    display: flex;
    align-items: center;
    color: rgb(92, 92, 24);
  }
}
.barra .derecha {
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .barra .derecha {
    margin-top: 0rem;
  }
}
.barra .derecha .dark-mode-boton {
  width: 3rem;
  margin-top: 3rem;
  margin-bottom: 3rem;
  filter: invert(100%);
}
.barra .derecha .dark-mode-boton:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .barra .derecha .dark-mode-boton {
    margin-right: 0;
  }
}

.navegacion {
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    opacity: 1;
    visibility: visible;
  }
}
.navegacion a {
  display: block;
  color: #000000;
  font-size: 1.6rem;
  margin: 0.5rem;
}
@media (min-width: 768px) {
  .navegacion a {
    margin: 1rem;
  }
}

/** Menú hamburguesa**/
.mobile-menu {
  cursor: pointer;
}
@media (min-width: 768px) {
  .mobile-menu {
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.mostrar {
  visibility: visible;
  opacity: 1;
}

@media (min-width: 768px) {
  .icono-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 2rem;
  }
}
.icono {
  max-height: 40rem;
}
.icono img {
  height: 10rem;
  width: auto;
  margin: auto;
}
.icono h3 {
  text-transform: uppercase;
  font-size: 2rem;
}
.icono p {
  padding: 0 2rem;
  font-size: 1.8rem;
}

.footer {
  padding: 1.5rem;
  background-color: rgb(40, 39, 51);
  color: white;
  margin-bottom: 0;
}
.footer .contenedor-footer {
  padding-top: 1.5rem;
}
.footer .navegacion {
  visibility: visible;
  opacity: 1;
}
.footer .copyright {
  text-align: center;
  color: #FFFFFF;
}
.footer a {
  color: #FFFFFF;
}
.footer a:hover {
  color: rgb(218, 243, 77);
}

.titulo-oferta {
  font-size: 5rem;
  color: red;
  text-transform: uppercase;
  border-bottom: 1rem solid red;
}

.seccion-anuncios {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .seccion-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .seccion-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 768px) {
  contenedor-anuncio {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  background-color: #faf8f8;
  border: 2px solid #f0e9e9;
}
.anuncio .foto-producto {
  width: fit-content;
  margin: auto;
}
.anuncio .foto-producto img {
  max-height: 30rem;
  padding: 2rem;
}
.anuncio .contenido-anuncio {
  padding: 0 1rem;
  margin: auto;
}
.anuncio h3,
.anuncio p {
  margin: auto;
}
.anuncio h4 {
  color: rgb(62, 12, 241);
  margin: 0;
}
.anuncio .precio {
  display: flex;
  justify-content: center;
  color: blue;
  font-weight: 900;
  font-size: 4rem;
  letter-spacing: 0.2rem;
  margin: 2rem;
}
.anuncio:hover {
  box-shadow: 0rem 0rem 15rem 1rem rgb(213, 211, 218);
  transition: 0.5s;
}

.icono-caracteristicas {
  display: block;
  list-style: none;
  padding: 0;
  margin: auto;
}
.icono-caracteristicas li {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(2, 1fr);
  padding: 1rem 2rem 1rem 0;
}
.icono-caracteristicas li img {
  display: block;
  margin-left: auto;
  margin-right: auto;
  max-height: 5rem;
}
.icono-caracteristicas li p {
  font-size: 1.6rem;
  margin-left: 0;
}

.boton-amarillo {
  background-color: #eef108;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.5rem;
  display: block;
  color: #000000;
  font-size: 2rem;
  padding: 0;
  margin: 1rem;
}
.boton-amarillo:hover {
  background-color: #d6d807;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: 700;
  text-decoration: none;
  padding: 1rem 3rem;
  text-align: center;
  margin-top: 1.5rem;
  display: in-block;
  color: #000000;
  font-size: 1.8rem;
  border: none;
}
.boton-verde:hover {
  background-color: #619800;
}

.no-webp .imagen-contacto {
  background-image: url("../img/Lubricantes/aceite-engranaje.webp");
}

.webp .imagen-contacto {
  background-image: url("../img/Lubricantes/aceite-engranaje.png");
}

.imagen-contacto {
  background-color: rgba(0, 0, 0, 0.5);
  background-position: center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.imagen-contacto h2 {
  color: #FFFFFF;
  letter-spacing: 0.1rem;
}
.imagen-contacto p {
  padding: 2rem;
  color: #FFFFFF;
  letter-spacing: 0.1rem;
}

.seccion-inferior {
  margin-top: 8rem;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
  }
}
.imagen {
  display: flex;
  align-items: center;
}

.entrada-blog {
  margin-bottom: 3rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    column-gap: 2rem;
  }
}
.entrada-blog:last-of-type {
  margin-bottom: 0;
}

.informacion-meta span {
  color: #71B100;
}

.texto-entrada {
  padding: 0 3rem 0 1rem;
}
.texto-entrada a {
  margin-top: 2rem;
}
.texto-entrada h4 {
  margin: 0;
  line-height: 2.2;
  text-align: left;
}
.texto-entrada h4::after {
  content: "";
  display: block;
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin-right: 2rem;
}
.texto-entrada p {
  text-align: left;
}

.testimonial {
  background-color: #71B100;
  font-size: 2.4rem;
  padding: 2rem;
  color: #FFFFFF;
  border-radius: 2rem;
}
.testimonial p {
  text-align: right;
  margin-right: 5rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/Iconos/comilla.svg);
  background-repeat: no-repeat;
  width: 6rem;
  height: 8rem;
  display: block;
  position: absolute;
  left: -2rem;
}

@media (min-width: 768px) {
  .contenido-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.texto-nosotros blockquote {
  font-weight: 900;
  font-size: 2.2rem;
  margin: 0;
  padding-left: 1rem 0 3rem 0;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2re 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: 700;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]),
.formulario textarea,
.formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  background-color: #FFFFFF;
  border: 1px solid #f0e9e9;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario textarea {
  height: 10rem;
}
.formulario .forma-contacto {
  max-width: 40rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}